import * as React from "react";
// import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import { IoClose } from "react-icons/io5";
import styled from "styled-components";
import {
  handleError,
  handleSuccess,
  handleWarning,
} from "../../../utils/Toastify";
import useAxios from "../../../api/Axios";
import ReactCrop from "react-image-crop";

const UploadProfileImage = ({ onReload, open, setOpen, info, url, image }) => {
  const axios = useAxios();
  const [classInfo, setClassInfo] = React.useState(null);
  const [loading, setLoading] = React.useState(true);
  const [myImage, setMyImage] = React.useState(null);
  const [crop, setCrop] = React.useState({
    unit: "%", // Can be 'px' or '%'
    x: 40,
    y: 40,
    width: 20,
    height: 30,
  });
  React.useEffect(() => {
    if (classInfo === null) {
      setClassInfo(info);
    }
    if (image !== null) {
      setMyImage(image);
    }
    setLoading(false);
  }, [info, classInfo, setClassInfo, image]);

  const handleClose = () => {
    setOpen(false);
  };

  const handleSubmit = (evt) => {};

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setClassInfo({ ...classInfo, [name]: value });
  };

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        {loading ? (
          <Loading />
        ) : (
          <React.Fragment>
            <Header>
              <HeaderText>Update Profile Image</HeaderText>
              <IconBox onClick={handleClose}>
                <IoClose />
              </IconBox>
            </Header>
            <DialogContent
              style={{
                display: "flex",
                flexWrap: "nowrap",
                alignItems: "center",
                padding: "0",
              }}
            >
              <Panel>
                <ImageBox>
                  <ReactCrop
                    aspect={1 / 1}
                    crop={crop}
                    minWidth="100px"
                    minHeight="100px"
                    keepSelection={true}
                    onChange={(c) => setCrop(c)}
                  >
                    <Image src={myImage} alt="avatar" />
                  </ReactCrop>
                </ImageBox>
                <input
                  style={{ display: "none" }}
                  id="oFile"
                  type="file"
                  accept="image/*"
                  onChange={(e) => {
                    const file = e.target.files[0];
                    const reader = new FileReader();
                    reader.readAsDataURL(file);
                    reader.onloadend = () => {
                      console.log(reader.result);
                      setMyImage(reader.result);
                    };
                  }}
                />
                <ButtonGroup style={{ justifyContent: "center" }}>
                  <UpLoad htmlFor="oFile">Upload</UpLoad>
                  <Button style={{ background: "green" }}>Remove</Button>
                  <Button style={{ background: "green" }}>Crop</Button>
                </ButtonGroup>
                <ButtonGroup>
                  <Button style={{ background: "red" }} onClick={handleClose}>
                    Cancel
                  </Button>
                  <Button
                    style={{ background: "green" }}
                    onClick={handleSubmit}
                  >
                    Update
                  </Button>
                </ButtonGroup>
              </Panel>
            </DialogContent>
          </React.Fragment>
        )}
      </Dialog>
    </div>
  );
};

export default UploadProfileImage;

const Header = styled.div`
  min-width: 300px;
  height: 45px;
  background: rgba(12, 36, 97, 1);
  display: flex;
  flex-wrap: nowrap;
  padding: 0.3rem 0.5rem;
  justify-content: space-between;
  align-items: center;
`;

const HeaderText = styled.p`
  font-weight: 500;
  font-size: 1.1rem;
  color: #fff;
  margin-bottom: 0px;
  padding: 0.2rem 0.5rem;
`;

const IconBox = styled.div`
  width: 35px;
  height: 25px;
  border-radius: 3px;
  outline: 2px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 1.5rem;

  &:hover {
    background: red;
    outline: 2px solid red;
  }
`;

export const Form = styled.form`
  padding: 1rem 2rem;
  width: 450px;
  border-radius: 15px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem 0;
  background: #fff;

  @media (max-width: 460px) {
    width: 100%;
    border: none;
  }
`;

export const FormHeader = styled.h1`
  @media (min-width: 540px) {
    font-size: 1.3rem;
  }
`;

export const InputGroup = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

export const Label = styled.label`
  font-size: 0.9rem;
  font-weight: 600;
`;
export const Input = styled.input`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);


  &:,
  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const Select = styled.select`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);

  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const SelectGroup = styled.div`
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  width: 100%;
`;

export const Span = styled.span`
  background: rgba(95, 39, 205, 01);
  color: #fff;
  border: 1px solid rgba(95, 39, 205, 01);
`;

export const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  width: 100%;
  justify-content: flex-end;
  padding: 0.5rem;

  @media (max-width: 560px) {
    flex-direction: column-reverse;
  }
`;

export const Button = styled.button`
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 600;
  min-width: 100px;
  color: #fff;
  font-size: 0.9rem;
  margin: 0.5rem;
  border-radius: 5px;
  &:nth-last-child(1) {
    margin-right: 0;
  }

  @media (max-width: 560px) {
    padding: 0.5rem 1rem;
    min-width: 95%;
    margin: 0.5rem 0;
    font-size: 1.1rem;
  }
`;

function Loading() {
  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "1rem",
      }}
    >
      <span
        style={{
          padding: "0.5rem 1rem",
          borderRadius: "5px",
        }}
      >
        Loading...
      </span>
    </div>
  );
}

const Panel = styled.div`
  display: flex;
  flex-direction: column;
  padding: 0.5rem 0;
  width: 450px;

  @media (max-width: 460px) {
    width: 100%;
  }
`;

const ImageBox = styled.div`
  width: 100%;
  height: 270px;
  background: rgba(3, 33, 3, 0.2);
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 0.5rem;
  @media (max-width: 460px) {
    height: 250px;
  }
`;

const Image = styled.img`
  width: auto;
  height: 100%;
`;

const UpLoad = styled.label`
  padding: 0.5rem 1rem;
  font-weight: 600;
  color: #fff;
  background: blue;
  width: 120px;
  text-align: center;
  border-radius: 5px;
  margin: 0.5rem;
  cursor: pointer;
`;
