import React from "react";
import styled from "styled-components";
import { MdModeEditOutline } from "react-icons/md";
import useAxios from "../api/Axios";
import { useAuth } from "../Context/Auth";
import { handleError, handleSuccess, handleWarning } from "../utils/Toastify";
import { useDispatch } from "react-redux";
import { setNavBarTitle } from "../reduxjs/TitleSlice";
import UploadProfileImage from "../components/assets/Edit/UploadProfileImage";

const Profile = () => {
  const axios = useAxios();
  const auth = useAuth();
  const dispatch = useDispatch();
  const [user, setUser] = React.useState(null);
  const [password, setPassword] = React.useState({
    oldPass: "",
    pass: "",
    confirmPass: "",
  });
  const [loading, setLoading] = React.useState(true);
  const [isImageUpload, setImageUpload] = React.useState(false);

  React.useEffect(() => {
    document.title = auth.user.name + " - Bangladesh Open University";
    dispatch(setNavBarTitle("Profile - " + auth.user.name));
    if (user === null) {
      getUser();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [axios]);

  const getUser = async () => {
    try {
      const url = "/teacher/profile?id=" + auth.user.id;
      await axios
        .get(url)
        .then((res) => {
          setUser(res.data);
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    } finally {
      setTimeout(() => {
        setLoading(false);
      }, 500);
    }
  };

  const handleChangeUserInfo = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };
  const handleChangePassword = (e) => {
    setPassword({ ...password, [e.target.name]: e.target.value });
  };
  const handleUserSubmit = async (evt) => {
    evt.preventDefault();
    let arr = [];

    for (const [key, value] of Object.entries(user)) {
      if (value === "" || value === undefined) {
        arr.push(key);
      }
    }
    if (arr.length === 0) {
      try {
        const url = "/teacher/update/info?id=" + auth.user.id;
        await axios
          .patch(url, user)
          .then((res) => {
            if (res.status === 201) {
              auth.user = {
                ...auth.user,
                name: user.teacherName,
              };
              handleSuccess(res.data);
            }
            if (res.status === 209) {
              handleWarning(res.data);
            } else {
              handleWarning(res.data.message);
            }
            getUser();
          })
          .catch((err) => {
            console.log(err);
            handleError(err?.response?.data?.message);
          });
      } catch (error) {
        handleError(error?.response?.data?.message);
      }
    } else {
      handleWarning(
        arr.length > 1
          ? `${arr.toString()} are required.`
          : `${arr.toString()} is required.`
      );
    }
  };
  const handlePasswordSubmit = async (evt) => {
    evt.preventDefault();
    let arr = [];

    for (const [key, value] of Object.entries(password)) {
      if (value === "" || value === undefined) {
        arr.push(key);
      }
    }

    if (arr.length === 0) {
      if (password.pass === password.confirmPass) {
        try {
          const url = "/teacher/update/password?id=" + auth.user.id;
          await axios.patch(url, password).then((res) => {
            if (res.status === 201) {
              getUser();
              handleSuccess(res.data);
            }
            if (res.status === 209) {
              document.getElementById("oldPass").innerHTML = res.data.message;
            } else {
              handleWarning(res.data.message);
            }
          });
        } catch (error) {
          handleError(error?.response?.data?.message);
        }
      } else {
        document.getElementById("password").innerHTML =
          "Confirm Password not Match";
      }
    } else {
      handleWarning(
        arr.length > 1
          ? `${arr.toString()} are required.`
          : `${arr.toString()} is required.`
      );
    }
  };
  return (
    <Container>
      {loading || user === null ? (
        <Loading />
      ) : (
        <Panel>
          <ColorPanel />
          <ImageBox>
            <Image
              src={auth.user.image || require("../components/images/man.png")}
            />
            <IconBox onClick={() => setImageUpload(true)}>
              <MdModeEditOutline />
            </IconBox>
            {isImageUpload && (
              <UploadProfileImage
                open={isImageUpload}
                setOpen={setImageUpload}
                onReload={getUser}
                url={"/teacher/upload/avatar?id=" + auth.user.id}
                image={
                  auth.user.image || require("../components/images/man.png")
                }
              />
            )}
          </ImageBox>
          <Text>{auth.user.name}</Text>
          <SubText>{user.study_center}</SubText>

          <Title>Account Setting</Title>
          <form
            onSubmit={handleUserSubmit}
            style={{ width: "100%", padding: "0.5rem 1rem" }}
          >
            <FlexColumn>
              <FlexColumn>
                <Label>Name</Label>
                <Input
                  type="text"
                  name="teacherName"
                  onChange={handleChangeUserInfo}
                  defaultValue={user.teacherName}
                  required
                />
              </FlexColumn>
              <FlexColumn>
                <Label>Email</Label>
                <Input
                  type="email"
                  name="userId"
                  onChange={handleChangeUserInfo}
                  defaultValue={user.userId}
                  required
                />
              </FlexColumn>
              <FlexColumn>
                <Label>Mobile Number</Label>
                <Input
                  type="tel"
                  name="mobile"
                  onChange={handleChangeUserInfo}
                  defaultValue={user.mobile}
                  required
                />
              </FlexColumn>
            </FlexColumn>
            <ButtonGroup>
              <Button barient="rgba(255, 71, 87,1)" type="button">
                Cancel
              </Button>
              <Button barient="rgba(55, 66, 250,1)" type="submit">
                Save
              </Button>
            </ButtonGroup>
          </form>
          <Title style={{ marginTop: "0.5rem" }}>Change Password</Title>
          <form
            onSubmit={handlePasswordSubmit}
            style={{ width: "100%", padding: "0.5rem 1rem" }}
          >
            <FlexColumn>
              <Label>Old Password</Label>
              <Input
                type="password"
                name="oldPass"
                defaultValue={password.oldPass}
                onChange={(e) => {
                  setPassword({ ...password, oldPass: e.target.value });
                  document.getElementById("oldPass").innerHTML = "";
                }}
                placeholder="Old Password"
                required
              />
              <PassError id="oldPass" />
            </FlexColumn>
            <FlexColumn>
              <Label>New Password</Label>
              <Input
                type="password"
                name="pass"
                defaultValue={password.pass}
                onChange={handleChangePassword}
                placeholder="New Password"
                required
              />
            </FlexColumn>
            <FlexColumn>
              <Label>Confirm Password</Label>
              <Input
                type="password"
                name="confirmPass"
                defaultValue={password.confirmPass}
                onChange={(e) => {
                  setPassword({ ...password, [e.target.name]: e.target.value });
                  document.getElementById("password").innerHTML = "";
                }}
                placeholder="Confirm Password"
                required
              />
              <PassError id="password"></PassError>
            </FlexColumn>
            <ButtonGroup>
              <Button barient="rgba(255, 71, 87,1)" type="button">
                Cancel
              </Button>
              <Button barient="rgba(55, 66, 250,1)" type="submit">
                Change
              </Button>
            </ButtonGroup>
          </form>
        </Panel>
      )}
    </Container>
  );
};

export default Profile;

const Loading = () => {
  return (
    <Panel>
      <ColorPanel />
      <ImageBox></ImageBox>
      <Text>Loading...</Text>
      <Title>Loading...</Title>
      <div style={{ width: "100%", padding: "0.5rem 1rem" }}>
        <FlexColumn>
          <Label>Loading..</Label>
          <Input
            style={{ marginBottom: "1rem" }}
            readOnly
            defaultValue="Loading..."
          />
          <Label>Loading..</Label>
          <Input
            style={{ marginBottom: "1rem" }}
            readOnly
            defaultValue="Loading..."
          />
          <Label>Loading..</Label>
          <Input readOnly defaultValue="Loading..." />
        </FlexColumn>
        <ButtonGroup style={{ marginTop: "2rem" }}>
          <Button barient="rgba(3,3,3, 0.4)">Loading...</Button>
          <Button barient="rgba(3,3,3, 0.4)">Loading...</Button>
        </ButtonGroup>
      </div>
    </Panel>
  );
};

const Container = styled.div`
  width: 100%;
  min-height: 85vh;
  padding-bottom: 1rem;
`;

const Panel = styled.div`
  display: flex;
  flex-direction: column;
  width: 450px;
  align-items: center;
  border: 1px solid rgba(3, 3, 3, 0.2);
  padding: 0.5rem 1rem;
  position: relative;
  padding-top: 40px;
  margin-bottom: 1rem;
  margin: auto;
  @media (max-width: 460px) {
    width: 100%;
  }
`;
const IconBox = styled.div`
  display: flex;
  width: 35px;
  height: 35px;
  align-items: center;
  justify-content: center;
  position: absolute;
  background: #fff;
  border-radius: 50%;
  border: 2px solid #333;
  bottom: 0;
  right: 0;
  cursor: pointer;
  font-size: 1.5rem;
`;
const ColorPanel = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 140px;
  background: linear-gradient(
    to left,
    rgba(16, 172, 132, 1),
    rgba(1, 163, 164, 1) 100%
  );
  z-index: -1;
  border: 1px solid rgba(3, 3, 3, 0.2);
`;

const ImageBox = styled.div`
  width: 130px;
  height: 130px;
  border-radius: 50%;
  border: 2px solid rgba(16, 172, 132, 1);
  outline: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
  background: rgba(16, 172, 132, 1);
  position: relative;
`;
const Image = styled.img`
  width: 95%;
  height: 95%;
  border-radius: 50%;
`;
const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 600;
  width: 100%;
  padding-left: 1rem;
`;
const Text = styled.p`
  font-size: 1.05rem;
  font-weight: 600;
  margin-bottom: 0;
`;
const SubText = styled.p`
  text-align: center;
`;

const FlexColumn = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 0.8rem;
`;

const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  width: 100%;
  margin-bottom: 1rem;
`;

const Label = styled.label`
  font-size: 1rem;
  font-weight: 600;
`;
const Input = styled.input`
  padding: 0.5rem 1rem;
  width: 100%;
  border: 1px solid rgba(3, 3, 3, 0.2);
  border-radius: 5px;
`;

const Button = styled.button`
  background: ${(props) => (props.barient ? props.barient : "blue")};
  color: #fff;
  font-weight: 600;
  border-radius: 5px;
  margin-left: 0.5rem;
  min-width: 100px;
  opacity: 0.5;
  &:hover {
    opacity: 1;
  }
`;

const PassError = styled.p`
  font-size: 0.9rem;
  color: red;
  text-align: right;
  font-weight: 500;
`;
